import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
    import siji from '@/views/modules/siji/list'
    import guyongjinjiaona from '@/views/modules/guyongjinjiaona/list'
    import tousujilu from '@/views/modules/tousujilu/list'
    import qianyuexinxi from '@/views/modules/qianyuexinxi/list'
    import cheliangxinxi from '@/views/modules/cheliangxinxi/list'
    import yijianfankui from '@/views/modules/yijianfankui/list'


//2.   
export const routes = [{
    path: '/',
    name: '',
    component: Index,
    children: [{
      // main
      path: '/',
      name: '',
      component: Home,
      meta: {icon:'', title:'center', affix: true}
    }, {
      path: '/updatePassword',
      name: '',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '',
      component: center,
      meta: {icon:'', title:'center'}
    }
      ,{
	path: '/siji',
        name: '',
        component: siji
      }
      ,{
	path: '/guyongjinjiaona',
        name: '',
        component: guyongjinjiaona
      }
      ,{
	path: '/tousujilu',
        name: '',
        component: tousujilu
      }
      ,{
	path: '/qianyuexinxi',
        name: '',
        component: qianyuexinxi
      }
      ,{
	path: '/cheliangxinxi',
        name: '',
        component: cheliangxinxi
      }
      ,{
	path: '/yijianfankui',
        name: '',
        component: yijianfankui
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '*',
    component: NotFound
  }
]
//3.VueRouter  
const router = new VueRouter({
  mode: 'hash',
  /*hashhistory*/
  routes //  routes: routes
})
const originalPush = VueRouter.prototype.push
//push
VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}
export default router;
